body { 
    font-family: 'Open Sans', sans-serif;
}

div.article-body {
    padding-top: 70px;
}

.login-background-image {
  position: fixed;
  left: 0;
  right: 0;
  z-index: 1;

  display: block;
  background-image: url('news.png');
  width: 1500px;
  height: 800px;

  filter: opacity(0.6) grayscale(100%);
}

.login-message {
  padding-top: 150px;
}

h1{
    font-weight: 700;
    color: #008080ff;
}
.login-content {
  position: fixed;
  left: 0;
  right: 0;
  padding-top: 100px;
  z-index: 9999;
}
.register-message {
  padding-top: 100px;
}
.register-content {
  position: fixed;
  left: 0;
  right: 0;
  padding-top: 200px;
  z-index: 9999;
}
.login-card {
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: -1px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
.form-signin input[type="triggers"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

header {
    font-weight: 500;
    font-size: 16pt;
}

a.navbar-brand {
    font-size: 20pt;
}

img.card-img-right {
    height: 150px;
    width: 150px; 
}

button.nf-btn {
    background-color: #008080ff;
    color: white;
}

div.home {
    background: news.jpg;
}
